import { graphql } from 'gatsby'
import _ from 'lodash'
import React, { useMemo } from 'react'
import { Head } from '../components/head'
import { HOME_TITLE } from '../constants'
import { useCategory } from '../hooks/useCategory'
import { useIntersectionObserver } from '../hooks/useIntersectionObserver'
import { useRenderedCount } from '../hooks/useRenderedCount'
import { useScrollEvent } from '../hooks/useScrollEvent'
import { Layout } from '../layout'
import * as Dom from '../utils/dom'
import * as EventManager from '../utils/event-manager'
import { About } from '../components/about'
import { Header } from '../components/header'
import { BlogContent } from '../components/blog-content'
import '../styles/style.scss'
import '../styles/mobile-style.scss'
import './index.scss'


const BASE_LINE = 80

function getDistance(currentPos) {
  return Dom.getDocumentHeight() - currentPos
}

export default ({ data, location }) => {
  const { siteMetadata } = data.site
  const { countOfInitialPost } = siteMetadata.configs
  const posts = data.popularPosts.edges
  const categories = useMemo(
    () => _.uniq(posts.map(({ node }) => node.frontmatter.category)),
    [],
  )
  const recentPosts = data.recentPosts.edges
  const jobs = data.jobs.edges
  const [count, countRef, increaseCount] = useRenderedCount()
  const [category, selectCategory] = useCategory()

  useIntersectionObserver()
  useScrollEvent(() => {
    const currentPos = window.scrollY + window.innerHeight
    const isTriggerPos = () => getDistance(currentPos) < BASE_LINE
    const doesNeedMore = () =>
      posts.length > countRef.current * countOfInitialPost

    return EventManager.toFit(increaseCount, {
      dismissCondition: () => !isTriggerPos(),
      triggerCondition: () => isTriggerPos() && doesNeedMore(),
    })()
  })

  return (
    <Layout location={location}
            title={siteMetadata.title}>
      <Head title={HOME_TITLE}
            keywords={siteMetadata.keywords} />
      <div className='container'>
        <Header
          header='home'
          text='By day, we create digital experiences for our clients and their users.'
        />
      </div>

      <div className='home'>
        <BlogContent
          showAll={false}
          posts={recentPosts}
          recentPosts={recentPosts}
          countOfInitialPost={countOfInitialPost}
          count={count}
          categories={categories}
          selectCategory={selectCategory}
          category={category}
          searchIndex={data.siteSearchIndex.index}
        />
        <About />
      </div>
    </Layout>
  )
}

export const pageQuery = graphql`
    query {
        siteSearchIndex {
            index
        }
        site {
            siteMetadata {
                title
                configs {
                    countOfInitialPost
                }
            }
        }
        popularPosts: allMarkdownRemark(
            sort: { fields: [frontmatter___date], order: DESC }
            filter: { frontmatter: { category: { ne: null }, draft: { eq: false }, popular: { eq: true }, type: { eq: "blog" } } }
            limit: 4
        ) {
            edges {
                node {
                    excerpt(pruneLength: 200, truncate: true)
                    fields {
                        slug
                    }
                    frontmatter {
                        date(formatString: "MMMM DD, YYYY")
                        title
                        tags
                        type
                        image {
                            childImageSharp {
                                fluid(maxWidth: 2000, quality: 100) {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                        authors {
                            name
                            id
                        }
                        category
                        draft
                    }
                }
            }
        }
        jobs: allMarkdownRemark(
            filter: { frontmatter: {  type: { eq: "job" } } }
        ) {
            edges {
                node {
                    id
                    frontmatter {
                        title
                        tags
                        type
                        category
                        image {
                            childImageSharp {
                                fluid(maxWidth: 2000, quality: 100) {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                }
            }
        }
        recentPosts: allMarkdownRemark(
            sort: { fields: [frontmatter___date], order: DESC }
            limit: 4
            filter: { frontmatter: {  type: { eq: "blog" } } }
        ) {
            edges {
                node {
                    id
                    fields {
                        slug
                    }
                    frontmatter {
                        date(formatString: "MMMM DD, YYYY")
                        title
                        tags
                        type
                        image {
                            childImageSharp {
                                fluid(maxWidth: 2000, quality: 100) {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                }
            }
        }
    }
`
